@keyframes rotates {
  from {
    rotate: 0;
  }

  to {
    rotate: 360deg;
  }
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

.stopwatch__minute-hand {
  left: inherit;
  z-index: -1;
  transform-origin: 1.5vmin 20vmin;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: auto;
  animation-name: rotates;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  top: 20vmin;
  right: 0;
}

.stopwatch__minute-hand--speed-up {
  transform-origin: 1.5vmin 20vmin;
  animation-name: rotates;
  animation-duration: 600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
}

.stopwatch__second-hand {
  left: inherit;
  z-index: 0;
  transform-origin: .75vmin 40vmin;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 40vmin;
  margin: auto;
  animation-name: rotates;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  right: 0;
  transform: translateY(2vmin);
}

.stopwatch__second-hand--speed-up {
  transform-origin: .75vmin 40vmin;
  animation-name: rotates;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.stopwatch__axis {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}
